import { Card, TaskItem } from "../../components";

function Home(params) {
  const tasks = ["test"];
  return (
    <>
      <Card
        isLoading={false}
        title={"Tamamlanmam grevlerim"}
        content={tasks.map((t, index) => {
          return <TaskItem key={index} />;
        })}
      />
      <Card
        isLoading={false}
        title={"Tamamlanm grevlerim"}
        content={tasks.map((t, index) => {
          return <TaskItem key={index} />;
        })}
      />
    </>
  );
}

export default Home;
